<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/practitioner/' + queries.practitioner)">
            <v-icon light left>mdi-close-circle-outline</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
      </v-list>
    </v-navigation-drawer>
    <fhir-page-questionnaire questionnaire="classification" page="classification" :pageId="queries.id">

    </fhir-page-questionnaire>
    <v-card
      class="mx-auto"
      max-width="700"
      outlined
      v-if="echelon"
    >
      <v-card-text>
        <center>
          <v-chip
            class="ma-2"
            color="green"
            text-color="white"
          >
            Echelon: {{ echelon }}
          </v-chip>
          <v-chip
            class="ma-2"
            color="primary"
            text-color="white"
          >
            Class: {{ salaryclass }}
          </v-chip>
          <br>
          <v-chip
            class="ma-2"
            color="darken-1 primary"
            label
            outlined
          >
            Basic Salary: {{ Intl.NumberFormat('en-US').format(basicSalary) }}
          </v-chip>
          <v-chip
            class="ma-2"
            color="darken-1 primary"
            label
            outlined
          >
            Gross Salary: {{ Intl.NumberFormat('en-US').format(gross) }}
          </v-chip>
          <v-chip
            class="ma-2"
            color="darken-1 primary"
            label
            outlined
          >
            Net Salary: {{ Intl.NumberFormat('en-US').format(net) }}
          </v-chip>
        </center>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
import { eventBus } from "@/main";
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      echelon: "",
      salaryclass: "",
      basicSalary: 0,
      bonus: 0,
      deduction: 0,
      net: 0,
      gross: 0
    }
  },
  components: {
    "fhir-page-questionnaire": () => import(/* webpackChunkName: "fhir-questionnaire" */ "@/views/fhir-page-questionnaire.vue" )
  },
  created() {
    eventBus.$on("Basic.extension[0]", (value) => {
      fetch(`/fhir/Basic?_id=${value.split("/")[1]}&_revinclude=Basic:categoryparent`).then((response) => {
        response.json().then((response) => {
          if(response.entry && response.entry.length) {
            let indexRes = response.entry.find((entry) => {
              return entry.resource.id === value.split("/")[1]
            })
            let echelonRes = response.entry.find((entry) => {
              return entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/echelon-profile")
            })
            let classRes = response.entry.find((entry) => {
              return entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/class-profile")
            })
            this.echelon = echelonRes.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
            }).valueString
            this.salaryclass = classRes.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
            }).valueString
            let salaryindex = indexRes.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
            }).valueString
            this.basicSalary = Math.floor(salaryindex*1272.7/12*1.2)
            this.gross = this.basicSalary + this.bonus
            this.net = this.gross - this.deduction
          }
        })
      })
    })
    eventBus.$on("Basic.extension[1]", (value) => {
      this.bonus = value
      this.gross = this.basicSalary + this.bonus
      this.net = this.gross - this.deduction
    })
    eventBus.$on("Basic.extension[2]", (value) => {
      this.deduction = value
      this.net = this.gross - this.deduction
    })
  }
}
</script>